import React, { useState ,useRef } from 'react';
import './App.css';

function padLeft(time){

  return time.toString().padStart(2, '0');
}

export default function App() {
  const [title, setTitle] = useState('Start your PRODUCTIVE session !');
  const [timeLeft, setTimeLeft] = useState(25 * 60);
  const [isRunning, setIsRunning] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const intervalRef  = useRef(null);

  const startTimer = () => {
    setIsRunning(true);
    setTitle("You're doing Great !")
    intervalRef.current =  setInterval( () =>  { 
        setTimeLeft( 
          timeLeft => {
            if(timeLeft >= 1) 
            {
              switch(timeLeft%29){
                case 2: {
                  setTitle("Failure will never overtake me if my determination to succeed is strong enough.");
                  break; 
                }
                case 7: {
                  setTitle("Clarity affords focus !");
                  break;
                }
                case 13:{
                  setTitle("Be brave to stand for what you believe in even if you stand alone !");
                  break;
                }
                case 23:{
                  setTitle("Everything is practice.");
                  break;
                }
              }
              return timeLeft - 1;
            }
            resetTimer();
            return 0;
          }
        );
      }, 1000
    );
  }

  const stopTimer = () => {
    setTitle("The path to success is to take massive, determined actions.")
    setIsRunning(false);
    setIsPaused(true);
    clearInterval(intervalRef.current);
  }

  const resetTimer = () => {
    setIsRunning(false);
    setIsPaused(false);
    setTimeLeft(25 * 60);
    clearInterval(intervalRef.current);
    setTitle('Are you ready for another Round ?')
  }

  const minutes = padLeft(Math.floor(timeLeft / 60));
  const seconds = padLeft(timeLeft - ( minutes * 60));

  return (
    <div className="app">
      <h1>Pomodoro!</h1>
      <h2> {title} </h2>
      <div className="timer">
        <span>{minutes}</span>
        <span>:</span>
        <span>{seconds}</span>
      </div>

      <div className="buttons">
        {!isRunning && <button onClick={startTimer}> {(isPaused === false) ? "Start" : "Resume" }</button>}
        {isRunning && <button onClick={stopTimer}>Stop</button> }
        {(isRunning || isPaused) &&  <button onClick= {resetTimer}>Reset</button>}
      </div>
    </div>
  );
}
